import * as actionTypes from '../actionTypes/contentTypes'
import {fromJS,List,Map} from 'immutable'

const defaultState = Map({
  value: '',
  arrowDirection: List([]),
  showDivPlace: '',
  showId: '',
  articles: List([]),
  tags: [],
  showTagContent: List([
    {id: 1, content: [1111]},
    {id: 2, content: [2222]},
    {id: 3, content: [3333]},
    {id: 4, content: [4444]},
    {id: 5, content: [5555]},
    {id: 6, content: [6666]},
    {id: 7, content: [7777]},
    {id: 71, content: [77877]},
    {id: 72, content: [77777]},
    {id: 73, content: [67777]},
    {id: 74, content: [77775]},
    {id: 75, content: [77747]},
    {id: 76, content: [77377]},
    {id: 77, content: [72777]},
    {id: 781, content: [1777]},
    {id: 783, content: [17777]},
    {id: 7812, content: [117777]},
    {id: 7834, content: [117777]},
  ]),
  menus:List(["Dashboard"]),
  subMenus:List([])
});
export default function contentReducer(state = defaultState, action) {
  switch (action.type) {
    case actionTypes.ARROW_DIRECTION:
      const clickIndex = state.get('menus').toJS().indexOf(action.arrowDirectionType);
      if(clickIndex>-1){
        let origin = state.get('arrowDirection');
        origin[clickIndex] = !origin[clickIndex];
        return state.set('arrowDirection', [...origin]);
      }
      return state;
    case actionTypes.CLOSE_TAG_DIV:
      return state.set('showDivPlace', fromJS('')).set('showId',fromJS(''));
    case actionTypes.LOAD_MENU:
      return state.set('menus',fromJS(action.menus))
        .set('arrowDirection', Array(action.menus.length).fill(false));//将数组元素初始化为false
    case actionTypes.LOAD_ARTICLES:
      return state.set('articles',List(action.articles));
    case actionTypes.LOAD_TAGS:
      return state.set('tags',fromJS(action.tags)).set('showDivPlace',fromJS(action.showDivPlace)).set('showId',fromJS(action.showId));
    case actionTypes.LOAD_SUBMENU:
      const menus = state.get('menus');
      const index = menus.indexOf(action.menuId);
      return state.setIn(['subMenus',index], List(action.subMenus));
    default:
      return state
  }
}