import axios from 'axios';

export function getMenus() {
  return axios.get('/literature/menus')
    .then((response) => response.data)
    .catch(() => console.log('getMenus'));
}

export function getSubMenus(menuId) {
  return axios.get('/literature/' + menuId.toLowerCase())
    .then(response => response.data)
    .catch(() => console.log('getSubMenus'));
}

export function getArticles(menuId, ele) {
  let result;
  switch (menuId) {
    case 'Dashboard':
      result = axios.get('/literature/allArticles')
        .then(response => {
          return response.data;
        }).catch(() => console.log('getArticles Dashboard error'));
      break;
    case 'Collections':
      const {collectionname} = ele;
       result = axios.get('/literature/collectionList/'+collectionname)
        .then(response => {
          return response.data;
        }).catch(() => console.log('getArticles Collections error'));
      break;
    case 'Tags':
      const tagName = ele.name;
      result = axios.get('/literature/tags/'+tagName)
        .then(response => {
          return response.data;
        }).catch(() => console.log('getArticles Tags error'));
        break;
    default:
      result = [];
      return {articles:result};
  }
  return result;
}

export function getTags(id) {
  return axios.get('/literature/tags4item/'+id)
    .then(response => {
      console.log('response.data',response.data);
      return response.data;
    }).catch(() => console.log('getArticles Tags error'));
}