import React, {PureComponent} from 'react'
import {connect} from 'react-redux'
import * as actionCreators from '../actionCreators/contentActionCreators'
import * as constValue from '../constValue'
import SubMenuWrap from './SubMenuWrap'
import {
  Content,
  ContentSide,
  ContentBody,
  Menu,
  Arrow,
  Body,
  TagDivUp,
  TagDivDown,
  H5
} from './contentStyle'
import TagDivWrap from './TagDivWrap'
import {fromJS} from "immutable";

class ContentWrap extends PureComponent {
  componentDidMount() {
    const {loadMenus} = this.props;
    loadMenus();
  }

  render() {
    const {arrowDirection,menus, showDivPlace, articles, arrowClick, showArticleMsg} = this.props;

    return (<Content>
      <ContentSide>
        <Menu>
          {
            menus.toJS().map((ele,index) => {
              return (<H5 onClick={arrowClick} id={ele} key={ele}><Arrow htmlFor={ele} />{ele}
                  {arrowDirection[index]?<SubMenuWrap menuId={ele}/>:null}</H5>)
            })
          }
        </Menu>
      </ContentSide>
      <ContentBody>
        {
          (articles&&!articles.isEmpty())?articles.map((ele) => {
              return (<Body
                id={ele.id}
                // onClick={showArticleMsg}
                onDoubleClick={showArticleMsg}
                key={ele.key}>
              {ele.itemtype}
              </Body>)
            }
          ):null
        }
        {showTagDiv(showDivPlace)}
      </ContentBody>
    </Content>)
  }
}

function showTagDiv(showDivPlace) {
  if (showDivPlace === constValue.SHOW_DIV_UP) {
    return (<TagDivUp><TagDivWrap/></TagDivUp>);
  } else if (showDivPlace === constValue.SHOW_DIV_DOWN) {
    return (<TagDivDown><TagDivWrap/></TagDivDown>);
  } else {
    return null;
  }
}

const mapStateToProps = (state) => {
  return {
    articles: state.contentReducer.get("articles"),
    showDivPlace: state.contentReducer.get("showDivPlace"),
    showId: state.contentReducer.get("showId"),
    arrowDirection: state.contentReducer.get("arrowDirection"),
    menus: fromJS(state.contentReducer.get("menus"))
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    arrowClick: (e) => {
      const id = e.target.id;
      let clickId = id;
      if (id) {
        const action = actionCreators.arrowDirectionChangeAction(id);
        dispatch(action);
      } else {
        const labelForId = e.target.getAttributeNode('for').value;
        clickId = labelForId;
        const action = actionCreators.arrowDirectionChangeAction(labelForId);
        dispatch(action);
      }
      const subMenuAction = actionCreators.creatSubMenuAction(clickId);
      dispatch(subMenuAction);
    },
    showArticleMsg: (e) => {
      const element = e.target;
      const id = element.id;
      const y = e.clientY;
      const innerHeight = window.innerHeight;
      let action,upOrDown;
      if (y / innerHeight < 0.5) {
        upOrDown = constValue.SHOW_DIV_DOWN;
      } else {
        upOrDown = constValue.SHOW_DIV_UP;
      }
      action = actionCreators.createShowTagAction(id,upOrDown);
      dispatch(action);
    },
    loadMenus: () => {
      const action = actionCreators.createFetchMenuAction();
      dispatch(action);
      const action_1 = actionCreators.createFetchArticlesAction();
      dispatch(action_1);
    }
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(ContentWrap);