import { put, fork, takeEvery, take, all } from 'redux-saga/effects'
import * as actionCreators from '../actionCreators/contentActionCreators'
import * as actions from '../actionTypes/contentTypes'
import * as api from './api';

function* loadMenusAsync() {
  yield takeEvery(actions.FETCH_MENU,fetchMenus);
}

function* fetchMenus() {
  const menus = yield api.getMenus();
  yield put(actionCreators.createLoadMenuAction(menus))
}
function* loadSubMenusAsync() {
  yield takeEvery(actions.FETCH_SUBMENU,fetchSubMenus);
}

function* fetchSubMenus() {
  const {menuId} = yield take(actions.FETCH_SUBMENU);
  const subMenus = yield api.getSubMenus(menuId);
  yield put(actionCreators.createLoadSubMenuAction(subMenus,menuId))
}

function* watchAndLog(){
  yield takeEvery('*',function* logger(action){
    // const state = yield select();
    // console.log('action', action);
    // console.log('state after', state)
  })
}

function* loadArticlesAsync() {
  yield takeEvery(actions.FETCH_ARTICLES,fetchArticles);
}

function* fetchArticles() {
  const {menuId,ele} = yield take(actions.FETCH_ARTICLES);
  const articles = yield api.getArticles(menuId,ele);
  yield put(actionCreators.createLoadArticlesAction(articles));
}

//CREATE_DOWN_DIV:SHOW_TAG
function* loadTagsAsync() {
  yield takeEvery(actions.SHOW_TAG, fetchTags);
}

function* fetchTags() {
  const {id,upOrDown} = yield take(actions.SHOW_TAG);

  const tags = yield api.getTags(id);
  yield put(actionCreators.createLoadTagsAction(tags,upOrDown,id));
}

function* mySaga() {
  yield all([
    fork(watchAndLog),
    fork(loadArticlesAsync),
    fork(loadTagsAsync),
    fork(loadMenusAsync),
    fork(loadSubMenusAsync),
    fork(fetchSubMenus),
    fork(fetchTags)
  ]);
}
export default mySaga;