import React, {PureComponent} from 'react'
import {connect} from 'react-redux'
import * as actionCreators from '../actionCreators/contentActionCreators'

import {
  SubMenu,
  SubMenus
} from './contentStyle'

class SubMenuWrap extends PureComponent {
  componentDidMount() {

  }
  render() {
    const {menus,menuId,subMenus, clickHandler} = this.props;
    const index =  menus.indexOf(menuId);
    const secondMenus = subMenus.get(index);
    return (<SubMenus onClick={clickHandler}>
      {
        secondMenus?secondMenus.map((ele)=>{
          if(menuId === 'Tags'){
            return <SubMenu onClick={(e)=>{clickHandler(e,ele)}} key={ele.name}>{ele.name}</SubMenu>
          } else if(menuId==='Collections'){
            return <SubMenu onClick={(e)=>{clickHandler(e,ele)}} key={ele.key}>{ele.collectionname}</SubMenu>
          } else if (menuId === 'Dashboard') {
            return <SubMenu onClick={(e)=>{clickHandler(e,ele)}} key={ele.name}>{ele.name}</SubMenu>
          } else {
            return null;
          }
        }):null
      }
    </SubMenus>);
  }
}

const mapStateToProps = (state) => {
  return {
    subMenus:state.contentReducer.get('subMenus'),
    menus: state.contentReducer.get('menus')
  }
};
const mapDispatchToProps = (dispatch,ownProps) => {
  return {
    clickHandler: (e,ele) => {
      e.stopPropagation();
      const {menuId} = ownProps;
      const action = actionCreators.createFetchArticlesAction(menuId, ele);
      dispatch(action);
    }
  }
};
export default connect(mapStateToProps, mapDispatchToProps)(SubMenuWrap);
